.project_card {
  background-color: white;
  background-color: var(--primary-white);
  background-size: cover;
  padding: 6rem 0 0;
  margin: 1rem;
  /* flex-grow: 1; */
  border-radius: 1rem;
  width: 36ch;
  max-width: 36ch;
  height: 20rem;
  overflow: hidden;
  transition: -webkit-transform 100ms ease;
  transition: transform 100ms ease;
  transition: transform 100ms ease, -webkit-transform 100ms ease;
  color: var(--White);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}

.project_card:hover,
.project_card:focus-within {
  -webkit-transform: scale(1.05) !important;
          transform: scale(1.05) !important;
}

.card_content {
  --padding: 1.5rem;
  padding: var(--padding);
  background: linear-gradient(
      hsl(0 0% 0% / 0),
      hsl(0 0% 0% / 0.25),
      hsl(0 0% 0% / 0.50),
      hsl(0 0% 0% / 0.65) 15%,
      hsl(0 0% 0% / 1)
      );
}


.project_card:hover .card_content > *:not(.card_title),
.project_card:focus-within .card_content > *:not(.card_title) {
  opacity: 1;
  transition-delay: 100ms;
}


.project_card:focus-within .card_content > *:not(.card_title) {
  transition-delay: 0ms;
}

.card_title {
  position: relative;
  width: -webkit-max-content;
  width: max-content;
  padding-bottom: 0.25rem;
  font-family: 'Roboto Slab', serif;
  text-transform: uppercase;
  color: white;
  color: var(--primary-white);
}

.card_title::after {
  content: "";
  position: absolute;
  left: calc(var(--padding) * -1 );
  bottom: 0;
  height: 4px;
  width: calc(100% + var(--padding));
  border-top-right-radius: 2rem;
  border-bottom-right-radius: 2rem;
  background: #ff4e00;
  background: var(--primary-orange);
  transition: -webkit-transform 400ms ease;
  transition: transform 400ms ease;
  transition: transform 400ms ease, -webkit-transform 400ms ease;
  -webkit-transform-origin: left;
          transform-origin: left;
  
}

.project_card:hover .card_title::after,
.project_card:focus-within .card_title::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
}

.card_body {
  color: rgb(255 255 255 / .95);
  padding-top: 0.5rem;
  padding-bottom: 1rem;
  font-family: 'Roboto Slab', serif;
  font-size: 1.25rem;
  text-shadow: 1px 1px black;
}

.button {
  cursor: pointer;
  display: inline;
  text-decoration: none;
  color: white;
  color: var(--primary-white);
  text-transform: uppercase;
  background-color: transparent;
  padding: 0.60rem 1.25rem;
  border-radius: 0.5rem;
  transition: all 1s ease;
  border: 2px solid white;
  border: 2px solid var(--primary-white);
  
}

.button:hover,
.button:focus { 
  color: white; 
  color: var(--primary-white);
  background-color: transparent;
  background-color: #ff4e00;
  background-color: var(--primary-orange);
  border: 2px solid white;
  border: 2px solid var(--primary-white);
}


